"use client"

import { createContext, useContext, useState, useCallback, type ReactNode, useEffect } from "react"
import type { User, UserRole } from "./types"
import { mockUsers } from "./mock-data"

interface AuthContextType {
  user: User | null
  isLoading: boolean
  login: (email: string, password: string, role?: UserRole) => Promise<boolean>
  signup: (data: SignupData) => Promise<boolean>
  logout: () => void
  isAuthenticated: boolean
}

interface SignupData {
  name: string
  email: string
  password: string
  role: UserRole
  walletAddress?: string
}

const AuthContext = createContext<AuthContextType | undefined>(undefined)

export function AuthProvider({ children }: { children: ReactNode }) {
  const [user, setUser] = useState<User | null>(null)
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    // Check for stored user on mount
    const storedUser = localStorage.getItem("dialexperts_user")
    if (storedUser) {
      setUser(JSON.parse(storedUser))
    }
    setIsLoading(false)
  }, [])

  const login = useCallback(async (email: string, _password: string, role?: UserRole): Promise<boolean> => {
    setIsLoading(true)
    // Simulate API call
    await new Promise((resolve) => setTimeout(resolve, 1000))

    // Find mock user by email or role
    let foundUser = mockUsers.find((u) => u.email === email)
    if (!foundUser && role) {
      foundUser = mockUsers.find((u) => u.role === role)
    }

    if (foundUser) {
      setUser(foundUser)
      localStorage.setItem("dialexperts_user", JSON.stringify(foundUser))
      setIsLoading(false)
      return true
    }

    setIsLoading(false)
    return false
  }, [])

  const signup = useCallback(async (data: SignupData): Promise<boolean> => {
    setIsLoading(true)
    // Simulate API call
    await new Promise((resolve) => setTimeout(resolve, 1500))

    const newUser: User = {
      id: `user-${Date.now()}`,
      name: data.name,
      email: data.email,
      walletAddress:
        data.walletAddress ||
        `${Math.random().toString(36).substring(2, 6)}...${Math.random().toString(36).substring(2, 6)}`,
      role: data.role,
      createdAt: new Date(),
    }

    setUser(newUser)
    localStorage.setItem("dialexperts_user", JSON.stringify(newUser))
    setIsLoading(false)
    return true
  }, [])

  const logout = useCallback(() => {
    setUser(null)
    localStorage.removeItem("dialexperts_user")
  }, [])

  return (
    <AuthContext.Provider
      value={{
        user,
        isLoading,
        login,
        signup,
        logout,
        isAuthenticated: !!user,
      }}
    >
      {children}
    </AuthContext.Provider>
  )
}

export function useAuth() {
  const context = useContext(AuthContext)
  if (context === undefined) {
    throw new Error("useAuth must be used within an AuthProvider")
  }
  return context
}
